import ChooseExamCenter, {
  LocationTableInterface,
} from "../Application/ExamCenter/ChooseExamCenter";
import { connect } from "react-redux";
import {
  Auth,
  loadExam,
  examDataInterface,
  PrepareExamsStoreInterface,
  loadExamsList,
  LocationsDataStoreInterface,
  loadAllLocationsData,
  setSelectedLocationData,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  candidateExamStoreInterface,
  setSelectedCandidateExam,
  courseStoreInterface,
  loadCourses,
  setSelectedCourse,
  InitializeExamDetails,
  InitialExamDetails,
  setSelectedPeriod,
  setSelectedPosition,
  positionsStoreInterface,
  loadExamsDone,
  schoolLevelsStoreInterface,
  FC_LoadCandidateContract,
  FC_getLocations,
  LocationAPI,
  DistrictLocationItem,
} from "../../actions";
import { StoreState } from "../../reducers";
import Loading from "../../components/Loading/Loading";
import {
  ExamInterface,
  examStatus,
  UserExamStatus,
} from "../../interfaces/exam";
import {
  CandidateClaimInterface,
  course,
  PlansTypeKeys,
  PositionAPI,
  PositionAPICourse,
} from "../../interfaces/index";
import {
  applicationExamAPI,
  applicationHistory,
  ApplicationPeriodGet,
  ApplicationStates,
  ApplicationStatesDescription,
  ApplicationWaitingStatus,
  GetApplicationsAPI,
} from "../../interfaces/application";
import { sentExamInterface } from "../Application/ExamCenter/ExamCenter";
import { BsFileEarmarkText, BsShieldCheck } from "react-icons/bs";
import {
  IoIosCheckmarkCircle,
  IoIosCheckmarkCircleOutline,
  IoMdTrash,
} from "react-icons/io";
import ApplicationHistory from "./ApplicationHistory/ApplicationHistory";
import ExamDetails from "./ExamDetails/ExamDetails";
import { CheckIfExamDone, findTotalMarks } from "../../utils/question_bank";
import PreparedExams from "./ExamStatus/PreparedExams";
import ActiveExams from "./ExamStatus/ActiveExams";
import ExamsDone from "./ExamStatus/ExamsDone";
import ClaimFormComponent from "../../components/ClaimForm/ClaimFormComponent";
import { ClaimingReducerType } from "../../reducers/claiming";
import { getMyClaims } from "../../actions/claiming";
import { ExamDoneDetailsAnswers } from "../CandidateExams/CandidateExams";
import CandidateAnswersDetails from "../../components/AnswersDetails/CandidateAnswersDetails";
import { RoomType } from "../../interfaces/location";
import { AiOutlineCheckCircle } from "react-icons/ai";
import { CandidatePlacementContract } from "../../interfaces/candidate_placement_contract";

interface PostExamsProps {
  doExam: examDataInterface;
  auth: Auth;
  loadExam: (user_id: string, exam: ExamInterface) => void;
  selectedPosition: PositionAPI;
  selectedExams: applicationExamAPI[] | null;
  selected_application: string | null;
  updateLocation: (application_id: string, exams: sentExamInterface[]) => void;
  changeLocationError: string | null;
  loading: boolean;
  incrementIncomplete: () => void;
  applicationHistory: applicationHistory[] | undefined;
  deleteApplication: (id: string) => void;
  selectedApplicationDetails: GetApplicationsAPI | null;
  locationsData: LocationsDataStoreInterface;
  loadAllLocationsData: (callback: (loading: boolean) => void) => void;
  setSelectedLocationData: (locationData: LocationTableInterface) => void;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  candidateExam: candidateExamStoreInterface;
  setSelectedCandidateExam: (selected_exam: InitialExamDetails | null) => void;
  course: courseStoreInterface;
  loadCourses: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
  InitializeExamDetails: (
    periodId: string,
    courseId: string,
    district_id: string,
    center_id: string,
    room_id: string,
    position: PositionAPI,
    start_exam: boolean,
    callback: (loading: boolean) => void
  ) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  positions: positionsStoreInterface;
  setSelectedPosition: (position: PositionAPI) => void;
  loadExamsDone: (
    periodId: string,
    userId: string,
    callback: (loading: boolean) => void
  ) => void;
  setMaxExamModal: (status: boolean) => void;
  setDisplayDetailsModal: (status: boolean) => void;
  claiming: ClaimingReducerType;
  getMyClaims: Function;
  setDoingExam: (status: boolean) => void;
  doing_exam: boolean;
  schoolLevels: schoolLevelsStoreInterface;
  FC_LoadCandidateContract: (
    user_id: string,
    position_id: string,
    session_id: string,
    callback: (
      error: boolean,
      contract: CandidatePlacementContract | null,
      msg: string
    ) => void
  ) => void;
}
interface PostExamsState {
  appointmentDetailsModal: {
    status: boolean;
    item: InitialExamDetails | null;
  };
  redirect: boolean;
  displayResults: boolean;
  changeLocation: {
    status: boolean;
    location: ExamInterface | null;
  };
  centersModal: boolean;
  selectedCourse: course | null;
  selectedLocation: applicationExamAPI | null;
  applicationHistoryState: boolean;
  loading: boolean;
  loadingExams: boolean;
  loadingExamsDone: boolean;
  claim: boolean;
  selectedExamDetails: ExamDoneDetailsAnswers | null;
  contract: CandidatePlacementContract | null;
  locations: LocationAPI | null;
}

class _PostExams extends Component<PostExamsProps, PostExamsState> {
  constructor(props: PostExamsProps) {
    super(props);

    this.state = {
      appointmentDetailsModal: {
        status: false,
        item: null,
      },
      redirect: false,
      displayResults: false,
      changeLocation: {
        status: false,
        location: null,
      },
      centersModal: false,
      selectedCourse: null,
      selectedLocation: null,
      applicationHistoryState: false,
      loading: false,
      loadingExams: false,
      loadingExamsDone: true,
      claim: false,
      selectedExamDetails: null,
      contract: null,
      locations: null,
    };
  }

  renderExam = (user_id: string, exam: ExamInterface) => {
    exam.status = examStatus.Editing;
    this.props.loadExam(user_id, exam);
    this.setState({ redirect: true });
  };

  setClaim = (status: boolean) => {
    this.setState({ claim: status });
  };

  closeModal = () => {
    this.setState({ centersModal: false });
    this.props.setMaxExamModal(false);
  };

  updateLocationFunction = (exams: sentExamInterface[]) => {
    if (this.props.selected_application !== null) {
      this.props.updateLocation(this.props.selected_application, exams);
    }
  };

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  setLoadingExams = (status: boolean) => {
    this.setState({ loadingExams: status });
  };
  setLoadingExamsDone = (status: boolean) => {
    this.setState({ loadingExamsDone: status });
  };

  showTotalMarks = (position: PositionAPI): boolean => {
    for (let item of position.courses) {
      let test = this.props.candidateExam.user_exams_done.find(
        (itm) => itm.course === item.course_id
      );
      if (test !== undefined) {
        if (
          test.user_status === UserExamStatus.Paused ||
          test.user_status === UserExamStatus.Editing
        ) {
          return false;
        }
      }
    }
    return true;
  };

  numberOfIncomplete = (
    exams: sentExamInterface[],
    courses: {
      course_name: string;
      course_code: string;
      description: string;
      school_levels: string[];
      course_id: string;
    }[]
  ) => {
    let count: number = 0;
    for (let i in courses) {
      if (
        exams.filter((item) => item.course_id === courses[i].course_id)
          .length === 0
      ) {
        count = count + 1;
      }
    }
    return count;
  };

  findLocation = (
    course_id: string,
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string
  ) => {
    return this.updateLocationFunction([
      {
        course_id: course_id,
        proctoring_type:
          location.rooms.find((itm) => itm.room_type === RoomType.Virtual) !==
          undefined
            ? RoomType.Virtual
            : RoomType.Physical,
        center_id: location.center_id,
        district_id: location.district_id,
      },
    ]);
  };

  findExam = (courseId: string) => {
    if (this.props.candidateExam.user_exams_temp !== null) {
      let current_temp = this.props.candidateExam.user_exams_temp.find(
        (itm) => itm.course === courseId
      );
      let testDone = this.props.candidateExam.user_exams_done.find(
        (itm) => itm.course === courseId
      );
      if (
        current_temp !== undefined &&
        (testDone === undefined ||
          testDone.user_status === UserExamStatus.Editing ||
          testDone.user_status === UserExamStatus.Paused ||
          testDone.user_status === UserExamStatus.Done ||
          testDone.user_status === UserExamStatus.Removed)
      ) {
        return current_temp;
      }
    }
    return null;
  };

  findCourse = (course_id: string): applicationExamAPI | null => {
    if (this.props.selectedExams !== null) {
      let x: applicationExamAPI | null | undefined =
        this.props.selectedExams.find(
          (itm) =>
            this.state.appointmentDetailsModal && itm.course_id === course_id
        );
      if (x !== undefined && x !== null) {
        return x;
      }
    }
    return null;
  };

  pendingCourses = () => {
    let count = 0;
    for (let course of this.props.selectedPosition.courses) {
      if (this.props.candidateExam.user_exams_temp !== null) {
        // let current_temp = this.props.candidateExam.user_exams_temp.find(itm => itm.course === course.course_id);
        let testDone = this.props.candidateExam.user_exams_done.find(
          (itm) => itm.course === course.course_id
        );
        if (testDone === undefined) {
          count = count + 1;
        }
      }
    }
    return count;
  };

  changeLocation = (item: PositionAPICourse) => {
    this.setState({ selectedCourse: item, centersModal: true });
    this.props.setMaxExamModal(true);
  };

  InitializationFn = () => {
    if (
      this.props.periods.selected_period !== null &&
      this.props.selectedPosition !== null
    ) {
      this.props.loadExamsDone(
        this.props.periods.selected_period._id,
        this.props.auth.user.user_id,
        this.setLoadingExamsDone
      );
      // if (this.state.loadingExamsDone === false) {
      for (let course of this.props.selectedPosition.courses) {
        let testDone = this.props.candidateExam.user_exams_temp.find(
          (itm) => itm.course === course.course_id
        );
        if (testDone === undefined) {
          let center_id = this.props.selectedExams?.find(
            (itm) => itm.course_id === course.course_id
          );
          if (center_id !== undefined) {
            this.props.setSelectedCourse(course);
            return this.props.InitializeExamDetails(
              this.props.periods.selected_period._id,
              course.course_id,
              center_id.district_id,
              center_id.center_id,
              center_id.room_id,
              this.props.selectedPosition,
              false,
              this.setLoadingExams
            );
          }
        }
      }
      // }
    }
  };

  componentDidMount = () => {
    this.props.loadApplicationPeriods(this.setLoading);
    if (
      this.props.course.courses === null ||
      this.props.course.courses.length === 0
    ) {
      this.props.loadCourses(this.setLoading);
    }
    this.InitializationFn();
    if (this.props.doExam.exam_answers) {
      if (this.props.doExam.exam_answers.status === examStatus.Editing) {
        this.setState({ redirect: true });
      }
    }
    if (
      this.props.claiming.myClaims === null &&
      this.props.periods.selected_period !== null
    ) {
      this.props.getMyClaims(
        this.props.auth.user.user_id,
        this.props.periods.selected_period._id,
        (status: boolean, msg: string, data?: CandidateClaimInterface[]) => {
          this.setState({ loading: status });
          if (msg !== "") {
            alert(msg);
          }
        }
      );
    }
    // Placement contract
    if (this.state.contract === null) {
      this.setState({ loading: true });
      this.props.selectedApplicationDetails !== null &&
        this.props.FC_LoadCandidateContract(
          this.props.auth.user.user_id,
          this.props.selectedApplicationDetails.position.position_id,
          this.props.selectedApplicationDetails.application_session._id,
          (status, data, msg) => {
            if (status === true && data !== null) {
              this.setState({ contract: data, loading: false });
              FC_getLocations(
                (
                  error: boolean,
                  locations: LocationAPI | null,
                  msg: string
                ) => {
                  if (locations !== null) {
                    this.setState({ locations: locations, loading: false });
                  }
                }
              );
            } else {
              window.alert(msg);
            }
            this.setState({ loading: false });
          }
        );
    }
  };

  GetDistrict = (district_code: string): DistrictLocationItem | null => {
    const temp = this.state.locations?.districts.find(
      (itm) => itm.district_code === district_code
    );
    if (temp !== undefined) {
      return temp;
    } else {
      return null;
    }
  };

  GetProvince = (district_code: string) => {
    if (this.GetDistrict(district_code) !== null) {
      const temp = this.state.locations?.provinces.find(
        (itm) =>
          itm.province_code === this.GetDistrict(district_code)?.province_code
      );
      if (temp !== undefined) {
        return temp;
      }
    }
    return null;
  };

  render() {
    if (
      this.props.candidateExam.selected_exam_id !== null &&
      this.props.candidateExam.selected_exam_details !== null &&
      (this.props.candidateExam.doing_exam === true ||
        this.props.doing_exam === true)
    ) {
      return <Redirect to="/do-exam" />;
    }
    if (
      this.state.redirect === true &&
      (this.props.candidateExam.doing_exam === true ||
        this.props.doing_exam === true)
    ) {
      return <Redirect to="/do-exam" />;
    }
    if (
      this.props.loading === true ||
      this.state.loadingExams === true ||
      this.state.loadingExamsDone === true
    ) {
      return (
        <div className="bg-white border my-4 mx-4 rounded px-4 py-4 flex flex-col">
          <span className="mx-4 animate-pulse">Loading, Please wait...</span>
          <Loading />
        </div>
      );
    }
    return this.props.selectedExams === null ? (
      "No result found!"
    ) : (
      <Fragment>
        {this.state.selectedExamDetails !== null ? (
          <>
            <div className="animate__animated animate__fadeInUp animate__faster mt-2 mx-2 rounded-t-md border border-blue-300">
              <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2 mb-2">
                <div className="flex flex-col w-full">
                  <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                    <span className="font-bold text-xl text-gray-800">
                      Exam answers details
                    </span>
                    <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                      <MdClose
                        className="cursor-pointer font-extrabold text-3xl"
                        onClick={() => {
                          this.setState({
                            selectedExamDetails: null,
                          });
                        }}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <CandidateAnswersDetails
                exam_id={this.state.selectedExamDetails}
                showAnswers={false}
              />
            </div>
          </>
        ) : this.state.appointmentDetailsModal.status === true &&
          this.state.appointmentDetailsModal.item !== null ? (
          <>
            <div className="animate__animated animate__fadeInUp animate__faster mt-2 mx-2 rounded-t-md border border-blue-300">
              <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2 mb-2">
                <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                  <span className="font-bold text-xl text-gray-800">
                    Exam:{" "}
                    {this.state.appointmentDetailsModal.item !== null && (
                      <>
                        {
                          this.findCourse(
                            this.state.appointmentDetailsModal.item!.course
                          )?.course_name
                        }
                      </>
                    )}
                  </span>
                  <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() =>
                        this.setState({
                          appointmentDetailsModal: {
                            status: false,
                            item: null,
                          },
                          displayResults: false,
                        })
                      }
                    />
                  </div>
                </div>
              </div>
              {this.findExam(this.state.appointmentDetailsModal.item.course) ===
              null ? (
                <div className="text-yellow-700 font-bold text-center text-3xl bg-yellow-50 border border-yellow-200 rounded px-4 py-4 mx-4 my-4">
                  No exam found for selected course
                </div>
              ) : this.props.periods.applicationPeriods &&
                (this.props.periods.applicationPeriods.find(
                  (itm) =>
                    this.state.appointmentDetailsModal.item &&
                    itm._id === this.state.appointmentDetailsModal.item.period
                )?.is_active === false ||
                  this.props.periods.applicationPeriods.find(
                    (itm) =>
                      this.state.appointmentDetailsModal.item &&
                      itm._id === this.state.appointmentDetailsModal.item.period
                  )?.is_active === undefined) ? (
                <div className="text-yellow-700 font-bold text-center text-3xl bg-yellow-50 border border-yellow-200 rounded px-4 py-4 mx-4 my-4 flex flex-col items-center">
                  <div>No exam for the selected period</div>
                  <div
                    onClick={() =>
                      this.props.loadApplicationPeriods(this.setLoading)
                    }
                    className="btn bg-white text-gray-700 w-max text-base border border-gray-700 mt-4 cursor-pointer"
                  >
                    Reload
                  </div>
                </div>
              ) : (
                <ExamDetails
                  locationDetails={this.props.selectedExams.find(
                    (itm) =>
                      this.state.appointmentDetailsModal.item &&
                      itm.course_id ===
                        this.state.appointmentDetailsModal.item.course
                  )}
                  examDetails={this.findExam(
                    this.state.appointmentDetailsModal.item.course
                  )}
                  locationsData={this.props.locationsData}
                  loadAllLocationsData={this.props.loadAllLocationsData}
                  setSelectedLocationData={this.props.setSelectedLocationData}
                  periods={this.props.periods}
                  loadApplicationPeriods={this.props.loadApplicationPeriods}
                  candidateExam={this.props.candidateExam}
                  setSelectedCandidateExam={this.props.setSelectedCandidateExam}
                />
              )}
            </div>
          </>
        ) : this.state.centersModal === true &&
          this.state.selectedCourse !== null ? (
          <div className="animate__animated animate__fadeInUp animate__faster mt-2 mx-2 rounded-t-md border border-blue-300">
            <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2 mb-2">
              <div className="flex flex-col w-full">
                <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                  <span className="font-bold text-xl text-gray-800">
                    Change exam location
                  </span>
                  <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() => {
                        this.setState({
                          centersModal: false,
                          selectedCourse: null,
                        });
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
            <ChooseExamCenter
              positionCourses={this.state.selectedCourse}
              addLocation={this.findLocation}
              closeModal={this.closeModal}
              changeProctoring={false}
              selectedEducationCategory={
                this.props.selectedPosition.education_institute_id
              }
            />
          </div>
        ) : this.state.applicationHistoryState === true ? (
          this.props.applicationHistory === undefined ? (
            <div className="text-yellow-700 font-bold text-2xl text-center">
              No application history found!
            </div>
          ) : (
            <div className="animate__animated animate__fadeInUp animate__faster bg-white mt-2 mx-2 rounded-t-md border border-blue-300">
              <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2">
                <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                  <span className="font-bold text-xl text-gray-800">
                    Application history
                  </span>
                  <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() =>
                        this.setState({ applicationHistoryState: false })
                      }
                    />
                  </div>
                </div>
              </div>
              <ApplicationHistory
                applicationHistory={this.props.applicationHistory}
              />
            </div>
          )
        ) : (
          <div className="">
            <div className="px-2 md:py-2">
              <div className="flex flex-col md:flex-row justify-between items-center md:items-center">
                <div className="flex flex-row items-center gap-3 mb-3 pt-4 lg:pt-0">
                  <span className="bg-blue-50  text-gray-600 font-base rounded-md px-3 py-1 mt-0">
                    Status:{" "}
                    <span className="font-extrabold text-blue-700 underline">
                      {this.props.selectedApplicationDetails &&
                        ApplicationStatesDescription.find(
                          (itm) =>
                            this.props.selectedApplicationDetails &&
                            itm.key ===
                              this.props.selectedApplicationDetails
                                .application_status
                        )?.caption}
                    </span>
                  </span>
                  {/* <div
                    onClick={() =>
                      this.setState({ applicationHistoryState: true })
                    }
                    className="btn bg-gray-100 text-gray-600 border border-gray-300 text-sm flex flex-row items-center gap-2 cursor-pointer hover:border-blue-500 hover:bg-blue-50 hover:text-blue-600 md:mt-0"
                  >
                    <MdHistory className="text-xl" />
                    <span>View application history</span>
                  </div> */}
                </div>
                <div
                  className={`grid ${
                    this.pendingCourses() > 0 ? "grid-cols-3" : "grid-cols-2"
                  } gap-4 w-full md:w-max bg-gray-100 md:bg-white p-2 md:p-0 rounded mb-2 md:mb-0`}
                >
                  <div className="col-span-1 flex justify-start md:justify-end">
                    <div className="flex flex-row items-center">
                      <div className="flex flex-col">
                        <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">
                          Total
                        </span>
                        <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">
                          {this.props.selectedExams.length}
                        </span>
                      </div>
                    </div>
                  </div>
                  {this.pendingCourses() > 0 && (
                    <div className="col-span-1 flex justify-start md:justify-end">
                      <div className="flex flex-row items-center">
                        <div className="flex flex-col">
                          <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">
                            Pending
                          </span>
                          <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">
                            {this.props.selectedPosition !== null &&
                              this.pendingCourses()}
                          </span>
                        </div>
                      </div>
                    </div>
                  )}
                  <div className="col-span-1 flex justify-start md:justify-end">
                    <div className="flex flex-row items-center">
                      <div className="flex flex-col">
                        <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">
                          Total marks
                        </span>
                        {this.showTotalMarks(this.props.selectedPosition) ===
                        true ? (
                          // this.state.loadingExams === true || this.state.loadingExamsDone === true ? <span>Wait...</span> :
                          <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">
                            {parseFloat(
                              findTotalMarks(
                                this.props.selectedPosition.courses,
                                this.props.candidateExam.user_exams_done
                              )
                            ) > 0
                              ? (
                                  parseFloat(
                                    findTotalMarks(
                                      this.props.selectedPosition.courses,
                                      this.props.candidateExam.user_exams_done
                                    )
                                  ) / this.props.selectedPosition.courses.length
                                ).toFixed(2)
                              : 0}
                            %
                          </span>
                        ) : (
                          <div
                            title="You can not view marks because one of your exams is not completed"
                            className="py-1 text-center bg-yellow-100 rounded w-max px-4 my-1"
                          >
                            Hidden
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="font-bold flex flex-col md:flex-row items-start md:items-center justify-start md:justify-between gap-2">
                {this.props.selectedApplicationDetails &&
                this.props.selectedApplicationDetails.application_status !==
                  ApplicationStates.Rejected ? (
                  <div className="text-xl flex flex-row items-center gap-2">
                    <BsFileEarmarkText className="text-2xl" />
                    <span>Exams list: </span>
                  </div>
                ) : (
                  <div>
                    <div className="bg-red-50 rounded p-4 w-full mt-2">
                      <div className="text-black font-bold text-xl mb-1">
                        Reason
                      </div>
                      <div className="text-lg font-light">
                        Position requirements are not matching with your
                        documents you attached during application process
                      </div>
                    </div>
                    <div
                      title="Click to send your claiming reasons"
                      className="bg-gray-200 text-gray-500 font-bold px-3 py-2 rounded cursor-not-allowed w-max mt-3"
                    >
                      Claiming is closed!
                    </div>
                  </div>
                  // <div className="hidden">
                  //   {this.state.loadingExams === false &&
                  //   this.state.loadingExamsDone === false &&
                  //   CheckIfExamDone(
                  //     this.props.selectedPosition.courses,
                  //     this.props.candidateExam.user_exams_done
                  //   ) === false &&
                  //   this.props.periods.selected_period &&
                  //   this.props.selected_application &&
                  //   this.props.claiming.myClaims &&
                  //   isClaimAllowed(
                  //     this.props.claiming.myClaims,
                  //     this.props.periods.selected_period._id,
                  //     this.props.selected_application
                  //   ) === true ? (
                  //     <div
                  //       onClick={() => this.setClaim(!this.state.claim)}
                  //       className="btn flex flex-row gap-2 items-center text-green-700 bg-green-50 cursor-pointer hover:bg-green-100 hover:text-green-900 border border-green-600 hover:border-green-800"
                  //     >
                  //       <IoHandLeftSharp className="text-xl " />
                  //       <span className="text-sm">Claim now</span>
                  //     </div>
                  //   ) : this.props.claiming.myClaims === null ? (
                  //     <div>Loading...</div>
                  //   ) : (
                  //     this.props.claiming.myClaims.length > 0 && (
                  //       <div
                  //         onClick={() => this.setClaim(!this.state.claim)}
                  //         className="btn flex flex-row gap-2 items-center text-white bg-blue-600 cursor-pointer hover:bg-blue-800"
                  //       >
                  //         <IoHandLeftSharp className="text-xl " />
                  //         <span className="text-sm">
                  //           {this.state.claim === true
                  //             ? "Hide details"
                  //             : "View claim details"}
                  //         </span>
                  //       </div>
                  //     )
                  //   )}
                  // </div>
                )}
                {/* {this.state.loadingExams === false &&
                  this.state.loadingExamsDone === false &&
                  CheckIfExamDone(
                    this.props.selectedPosition.courses,
                    this.props.candidateExam.user_exams_done
                  ) === false && (
                    <div
                      onClick={
                        () =>
                          this.props.selected_application &&
                          this.props.deleteApplication(
                            this.props.selected_application
                          )
                        // alert("You are not allowed to remove application")
                      }
                      className="btn flex flex-row gap-2 items-center text-red-700 bg-red-50 cursor-pointer hover:bg-red-100 hover:text-red-900"
                    >
                      <IoMdTrash className="text-xl " />
                      <span className="text-sm">Remove application</span>
                    </div>
                  )} */}
              </div>
              {this.state.claim === true &&
                this.props.selected_application !== null && (
                  <div className="bg-gray-100 mt-4 pt-2 rounded">
                    <ClaimFormComponent
                      setFormState={this.setClaim}
                      claim_type={PlansTypeKeys.APPLICATION}
                      file_id={this.props.selected_application}
                    />
                  </div>
                )}
            </div>
            {this.props.selectedApplicationDetails &&
              this.props.selectedApplicationDetails.application_status !==
                ApplicationStates.Rejected && (
                <>
                  <ActiveExams
                    selectedPosition={this.props.selectedPosition}
                    selectedExams={this.props.selectedExams}
                    findExam={this.findExam}
                    loadingExams={this.state.loadingExams}
                    loadingExamsDone={this.state.loadingExamsDone}
                    setMaxExamModal={this.props.setMaxExamModal}
                    course={this.props.course}
                    setSelectedCourse={this.props.setSelectedCourse}
                    candidateExam={this.props.candidateExam}
                    positions={this.props.positions}
                    periods={this.props.periods}
                    setSelectedPosition={this.props.setSelectedPosition}
                    setSelectedCandidateExam={
                      this.props.setSelectedCandidateExam
                    }
                    selectedApplicationDetails={
                      this.props.selectedApplicationDetails
                    }
                    setDoingExam={this.props.setDoingExam}
                    InitializeExamDetails={this.props.InitializeExamDetails}
                    setLoadingExams={this.setLoadingExams}
                  />
                  <PreparedExams
                    selectedPosition={this.props.selectedPosition}
                    selectedExams={this.props.selectedExams}
                    findExam={this.findExam}
                    loadingExams={this.state.loadingExams}
                    loadingExamsDone={this.state.loadingExamsDone}
                    setMaxExamModal={this.props.setMaxExamModal}
                    course={this.props.course}
                    setSelectedCourse={this.props.setSelectedCourse}
                    candidateExam={this.props.candidateExam}
                    positions={this.props.positions}
                    periods={this.props.periods}
                    setSelectedPosition={this.props.setSelectedPosition}
                    setSelectedCandidateExam={
                      this.props.setSelectedCandidateExam
                    }
                    changeLocation={this.changeLocation}
                    selectedApplicationDetails={
                      this.props.selectedApplicationDetails
                    }
                    setDoingExam={this.props.setDoingExam}
                    InitializeExamDetails={this.props.InitializeExamDetails}
                    setLoadingExams={this.setLoadingExams}
                  />
                  <ExamsDone
                    selectedPosition={this.props.selectedPosition}
                    selectedExams={this.props.selectedExams}
                    findExam={this.findExam}
                    loadingExams={this.state.loadingExams}
                    loadingExamsDone={this.state.loadingExamsDone}
                    setMaxExamModal={this.props.setMaxExamModal}
                    course={this.props.course}
                    setSelectedCourse={this.props.setSelectedCourse}
                    candidateExam={this.props.candidateExam}
                    positions={this.props.positions}
                    periods={this.props.periods}
                    setSelectedPosition={this.props.setSelectedPosition}
                    setSelectedCandidateExam={
                      this.props.setSelectedCandidateExam
                    }
                    viewMarksDetails={(
                      selectedExamDetails: ExamDoneDetailsAnswers
                    ) => {
                      this.setState({
                        selectedExamDetails: selectedExamDetails,
                      });
                    }}
                  />
                </>
              )}
            {/* Preferences/candidate agreement */}
            {this.props.selectedApplicationDetails !== null &&
              this.props.selectedApplicationDetails.application_status ===
                ApplicationStates.Shortlisted && (
                <div className="my-3 mx-2">
                  <div className="flex flex-row items-center justify-between gap-3">
                    <div className="flex flex-row items-center gap-2">
                      <div>
                        <AiOutlineCheckCircle className="text-3xl" />
                      </div>
                      <span className="text-xl font-extrabold">
                        Placement details
                      </span>
                    </div>
                  </div>
                  {/* Details */}
                  <div className="grid md:grid-cols-12 gap-4 mt-4">
                    {(this.props.selectedApplicationDetails.waiting_list ===
                      ApplicationWaitingStatus.WAIT_FOR_PUBLICATION ||
                      this.props.selectedApplicationDetails.waiting_list ===
                        undefined) && (
                      <div className="col-span-12 w-full px-3 py-5 rounded bg-gray-100 font-bold text-gray-800 text-2xl flex flex-col items-center justify-center">
                        <div>
                          <MdOutlinePendingActions className="text-8xl text-gray-800" />
                        </div>
                        <span className="text-center font-light">
                          Wait for placement decision!
                        </span>
                      </div>
                    )}
                    {this.props.selectedApplicationDetails.waiting_list ===
                      ApplicationWaitingStatus.WAITING_LIST ||
                      (this.props.selectedApplicationDetails.waiting_list ===
                        ApplicationWaitingStatus.PLACED && (
                        <div className="col-span-6">
                          <div className="bg-gray-100 rounded-md p-3">
                            <div className="font-light text-xl">
                              My preferences
                            </div>
                            <div className="mt-3">
                              {this.state.contract === null ? (
                                <div>Loading...</div>
                              ) : (
                                this.state.locations !== null && (
                                  <div>
                                    {this.state.contract.district.values.map(
                                      (item, i) => (
                                        <div
                                          key={i + 1}
                                          className="flex flex-row items-center gap-2 border border-gray-400 bg-white mb-2 rounded-lg overflow-hidden animate__animated animate__fadeIn"
                                        >
                                          <div className="bg-gray-200 rounded-l-md flex items-center justify-center h-16 w-16">
                                            <MdModeStandby className="text-4xl text-gray-500" />
                                          </div>
                                          <div className="flex flex-col">
                                            <div className="flex flex-row items-center gap-2">
                                              <span className="text-gray-500">
                                                Province:
                                              </span>
                                              <span className="font-bold">
                                                {
                                                  this.GetProvince(
                                                    item.toString()
                                                  )?.province_name
                                                }
                                              </span>
                                            </div>
                                            <div className="flex flex-row items-center gap-2">
                                              <span className="text-gray-500">
                                                District:
                                              </span>
                                              <span className="font-bold">
                                                {
                                                  this.GetDistrict(
                                                    item.toString()
                                                  )?.district_name
                                                }
                                              </span>
                                            </div>
                                          </div>
                                        </div>
                                      )
                                    )}
                                    <div>
                                      {this.state.contract.district.strict ===
                                        false && (
                                        <div className="bg-gray-500 text-white px-3 py-1 rounded-2xl w-max">
                                          And other districts
                                        </div>
                                      )}
                                    </div>
                                  </div>
                                )
                              )}
                            </div>
                          </div>
                        </div>
                      ))}
                    {this.props.selectedApplicationDetails.waiting_list ===
                      ApplicationWaitingStatus.FAILED && (
                      <div className="col-span-12 w-full px-3 py-5 rounded bg-yellow-100 font-bold text-yellow-800 text-2xl flex flex-col items-center justify-center">
                        <div>
                          <MdOutlineSmsFailed className="text-8xl text-yellow-800" />
                        </div>
                        <span className="text-center font-light">
                          We are sorry, you can not be placed for this session.
                        </span>
                      </div>
                    )}
                    {this.props.selectedApplicationDetails.waiting_list ===
                      ApplicationWaitingStatus.WAITING_LIST && (
                      <div className="col-span-6 w-full px-3 py-5 rounded bg-blue-100 font-bold text-blue-600 text-2xl flex flex-col items-center justify-center">
                        <div>
                          <IoIosCheckmarkCircleOutline className="text-8xl text-blue-600" />
                        </div>
                        <span className="text-center font-light">
                          You are on waiting list, please wait to be placed when
                          vacant posts are available!
                        </span>
                      </div>
                    )}
                    {this.props.selectedApplicationDetails.waiting_list ===
                      ApplicationWaitingStatus.PLACED && (
                      <div className="col-span-6">
                        <div className="rounded-md bg-white p-4 flex flex-col gap-2 w-full h-full">
                          <div className="flex flex-col items- gap-2 border border-blue-400 bg-white mb-2 rounded-lg overflow-hidden animate__animated animate__fadeIn w-full">
                            <div className="bg-blue-100 flex flex-row items-center gap-2 h-max w-full p-2">
                              <div>
                                <IoIosCheckmarkCircle className="text-4xl text-blue-500" />
                              </div>
                              <div className="text-blue-600 font-light text-xl">
                                My placement
                              </div>
                            </div>
                            <div className="flex flex-col p-3">
                              <div className="flex flex-row items-center gap-2">
                                <span className="text-gray-500">Province:</span>
                                <span className="font-bold">Kigali city</span>
                              </div>
                              <div className="flex flex-row items-center gap-2">
                                <span className="text-gray-500">District:</span>
                                <span className="font-bold">Nyarugenge</span>
                              </div>
                              <div className="flex flex-row items-center gap-2">
                                <span className="text-gray-500">Sector:</span>
                                <span className="font-bold">Kimisagara</span>
                              </div>
                              <div className="flex flex-row items-center gap-2 px-4 py-1 rounded-2xl bg-blue-600 text-white w-max mt-2">
                                <span className="text-blue-50">School:</span>
                                <span className="font-bold">EP Gitega</span>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              )}
          </div>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  doExam,
  prepareExam,
  locationsData,
  periods,
  candidateExam,
  course,
  positions,
  claiming,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  doExam: examDataInterface;
  prepareExam: PrepareExamsStoreInterface;
  locationsData: LocationsDataStoreInterface;
  periods: applicationPeriodsStoreInterface;
  candidateExam: candidateExamStoreInterface;
  course: courseStoreInterface;
  positions: positionsStoreInterface;
  claiming: ClaimingReducerType;
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return {
    auth: auth,
    doExam: doExam,
    prepareExam: prepareExam,
    locationsData: locationsData,
    periods: periods,
    candidateExam: candidateExam,
    course: course,
    positions: positions,
    claiming: claiming,
    schoolLevels,
  };
};

const PostExams = connect(mapStateToProps, {
  loadExam,
  loadExamsList,
  loadAllLocationsData,
  setSelectedLocationData,
  loadApplicationPeriods,
  setSelectedCandidateExam,
  loadCourses,
  setSelectedCourse,
  InitializeExamDetails,
  setSelectedPeriod,
  setSelectedPosition,
  loadExamsDone,
  getMyClaims,
  FC_LoadCandidateContract,
})(_PostExams);

export default PostExams;
