import React, { Component } from "react";
import { GetApplicationsAPI } from "../../interfaces/application";
import { CandidateInterface } from "../../interfaces/candidatesInterfaces";
import { PlacedCandidateStatus } from "../../interfaces/marks_publication";
import { UserInfoInterface } from "../../shared/interfaces";

interface CandidatePlacementCheckProps {
  position: GetApplicationsAPI;
  user: CandidateInterface;
}
interface CandidatePlacementCheckState {
  loading: boolean;
}
interface SchoolItemPlacement {
  _id: string;
  school_name: string;
  school_code: string;
  province_id: string;
  province_code: string;
  district_id: {
    _id: string;
    district_name: string;
  };
  district_code: string;
  sector_code: string;
  sector_id: string;
  school_status: string;
  status: number;
  createdAt: string;
  updatedAt: string;
}
export interface SchoolPlacementInterface {
  n_id: string;
  phone_number: string;
  email: string;
  user_info: UserInfoInterface;
  placed: {
    _id: string;
    comment: string;
    status: PlacedCandidateStatus;
    waiting_list_id: string;
    user_id: string;
    school_id: string;
    district_id: string;
    position_id: string;
    request_id: string;
    createdAt: string;
    updatedAt: string;
    school: SchoolItemPlacement;
    __v: number;
  };
}

// interface CandidatePlacementSchool {}

export class CandidatePlacementCheck extends Component<
  CandidatePlacementCheckProps,
  CandidatePlacementCheckState
> {
  constructor(props: CandidatePlacementCheckProps) {
    super(props);

    this.state = {
      loading: false,
    };
  }
  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  render() {
    return <div>Candidate Placement check</div>;
  }
}

export default CandidatePlacementCheck;
